.main {
  --main-color:      #{$black};
  --secondary-color: #{$gray};
  $this: &;
  @include image('bg.jpg');
  background-repeat: repeat-y;
  background-size: auto;
  position: relative;
  z-index: 1;
  padding-bottom: var(--section-padding);
  padding-top: var(--section-padding);
  opacity: 0;
  &.has-aside-open {
    .aside {
      transform: translate(0, 0);
      position: relative; }
    #{$this}__aside {
      transform: translate(0, 0);
      @include lg {
        position: fixed;
        overflow: auto;
        z-index: 10;
        top: 0;
        padding-top: $header-height;
        background-color: rgba($black, .5);
        width: 100%; }
      @include lg {
        padding-top: $header-height-mob; } }
    #{$this}__content {
      transform: translate($aside-width, 0);
      padding-right: $aside-width;
      @include lg {
        transform: translate(0, 0);
        padding-right: 0;
 } } }        // z-index: 0
  &.has-aside-open-mob {
    #{$this}__content {
      @include lg {
          z-index: 0; } }
    #{$this}__aside {
      z-index: 10; } }
  &__aside {
    position: absolute;
    z-index: 2;

    top: 0;
    left: 0;
    height: 100%;
    background-color: $white;
    border-bottom: 1px solid  $c-border;
    transform: translate(-100%, 0);
    @include tr(transform .3s);

    @include lg {
      background-color: transparent;
      @include tr(transform 0s, background-color .3s);
      transform: translate(0, 0);
      z-index: 0;
      .aside {
        transform: translate(-100%, 0); } } }

  &__content {
    position: relative;
    z-index: 1;
    @include tr(transform .3s, padding .3s);
    @include lg {
      position: relative;
      z-index: 1; } }
  &__hero {
    @include cover;
    padding-top: 25%;
    position: absolute;
    z-index: 0;
    top: 0;
    left: 0;
    width: 100%;
    @include md {
      padding-top: 30%; } }

  &--full {
    padding-top: $subnav-height;
    margin-top: $header-height;
    @include lg {
      margin-top: $header-height-mob;
      padding-top: 50px;
      .content {
        margin-top: -8px; } }
    #{$this}__aside {
      top: $subnav-height;
      height: calc(100% - #{$subnav-height});
      @include lg {
        top: 0;
        height: 100%; } }
    #{$this}__content {
 } } }      // padding-left: 50px

.content {
  max-width: 1080px;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
  &__row {
    @include flex(flex-start, normal, row, wrap);
    margin-left: -10px;
    margin-right: -10px;
    margin-bottom: 20px; }
  &__row-align-end {
    align-items: flex-end; }
  &__col {
    width: 50%;
    padding-left: 10px;
    padding-right: 10px;
    @include md {
      width: 100%; } }
  &__table {
    margin-bottom: 50px;
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 15px;
      overflow: auto; } }
  &__title {
    margin-bottom: 30px;
    text-align: center; }
  &__breadcrumbs {
    position: relative;
    left: 50px;
    width: calc(100% - 50px);
    @include lg {
      position: relative;
      top: -35px; } }
  &__img {
    @include md {
      img {
        max-width: 50%; } } }

  &--full {
    max-width: 1440px; } }

.content-list {
  font-size: 0;
  text-align: left;
  li {
    display: inline-block;
    vertical-align: top;
    width: 50%;
    font-size: $fz;
    padding-right: 10px;
    @include notlast {
      margin-bottom: 15px; }
    @include xllopen {
      width: 100%; }
    @include lg {
      width: 100%;
      @include notlast {
        margin-bottom: 0px; } }
    @include md {
      font-size: $fz-md; }
    &::before {
      content: '+';
      font-size: 1.8em;
      display: inline-block;
      position: relative;
      top: .2em;
      margin-right: .2em;
      color: var(--main-color); } } }


.content-block {
  --padding: 20px;
  @include xl {
    --padding: 15px; }
  @include sm {
    --padding: 10px; }
  background-color: $white;
  padding: var(--padding);
  &__title {
    margin-bottom: 15px;
    @include md {
      margin-bottom: 5px; } }
  &__block {
    margin-left: calc(0px - var(--padding));
    margin-right: calc(0px - var(--padding));
    padding: var(--padding);
    @include notlast {
      border-bottom: 1px solid  $c-border; } }
  &__block-flex {
    @include flex(flex-start, center, row, wrap);
    @include xllopen {
 }      // display: block
    @include lg {
 } }      // display: block
  &__icons {
    background-color: var(--secondary-color);
    padding: 40px 25px 25px;
    margin-bottom: 15px; }
  &__img {
    width: 25%;
    @include xllopen {
      margin-bottom: 15px;
      margin-right: 10px; }
    @include xl {
      width: 15%;
      margin-right: 10px; }
    @include lg {
      // width: 25%
 }      // margin-bottom: 15px
    @include md {
 } }      // margin-bottom: 5px
  &__title {
    flex: 1;
    padding-right: 10px; }
  &__price {
    width: 30%;
    @include xllopen {
      width: 100%;
      text-align: center; }
    @include lg {
      width: 100%;
      text-align: center; } } }
