.table__char { width: 35.06944%; padding: 0; }

.table--inner { border: none; background-color: transparent; display: block; }

.table--inner .table__td, .table--inner .table__th { padding: 0; display: flex; align-items: center; justify-content: center; }

.table--inner .table__tr { border: 0; display: flex; }

.item { padding: 15px; box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.12); background-color: #fff; display: flex; justify-content: space-between; align-items: normal; flex-direction: column; flex-wrap: nowrap; }

.item__img { text-align: center; }

.item__img img { display: inline-block; }

.item__img, .item__content { border-bottom: 1px solid #d7d7d7; display: block; padding-top: 10px; padding-bottom: 10px; }

.item__content { flex-grow: 1; }

.item__bottom { display: flex; justify-content: space-between; align-items: center; flex-direction: row; flex-wrap: nowrap; padding-top: 10px; }

@media only screen and (max-width: 1599px) { .has-aside-open .item__bottom { display: block; }
  .has-aside-open .item__bottom .btn { min-width: 0; width: 100%; } }

.item-list dl:after { content: " "; display: table; clear: both; }

.item-list dt { font-family: 'proximanova-light', Arial, sans-serif; font-size: 14px; color: #939598; float: left; margin-right: 5px; }

.item-list dd { font-family: 'proximanova-regular', Arial, sans-serif; font-size: 14px; }

.item-list em { font-style: normal; color: #b9282e; }

.slider__wrap { --arrows-gap: -30px; position: relative; }

.slider__prev, .slider__next { position: absolute; top: 50%; background-color: transparent; }

.slider__prev { width: 15px; height: 15px; border-left: 1px solid #191919; border-bottom: 1px solid #191919; transform: rotate(45deg); display: inline-block; transform: translate(0, -50%) rotate 45deg; left: var(--arrows-gap); }

.slider__next { width: 15px; height: 15px; border-left: 1px solid #191919; border-bottom: 1px solid #191919; transform: rotate(-135deg); display: inline-block; transform: translate(0, -50%) rotate 45deg; right: var(--arrows-gap); }

.slider--similar .slick-track { display: flex; justify-content: flex-start; align-items: normal; flex-direction: row; flex-wrap: nowrap; }

.slider--similar .slick-slide { display: flex; justify-content: flex-start; align-items: normal; flex-direction: column; flex-wrap: nowrap; height: auto; }

.slider--similar .slick-slide > div { height: 100%; }

.slider--similar .slick-slide .slide { height: 100%; display: flex !important; }

.slider--assort__wrap { padding-top: 45px; }

.slider--assort__btns { position: absolute; bottom: 50px; display: inline-block; left: calc(30% - 50px); }

.slider--assort__counter { position: absolute; top: 0; left: -.5em; }

.slider--assort__counter-sm { position: absolute; left: 0; bottom: 100px; }

.assort-slide { display: flex !important; display: flex; justify-content: flex-start; align-items: center; flex-direction: row; flex-wrap: nowrap; }

.assort-slide__title { margin-bottom: 25px; }

.assort-slide__content { width: 30%; padding-right: 5%; }

.assort-slide__img { width: 70%; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; padding-top: 40%; }

.assort-slide__text { text-align: left; }

.arrow { width: 100px; height: 50px; background-color: #b9282e; color: #fff; display: inline-flex; justify-content: center; align-items: center; font-size: 40px; }

.arrow .icon { fill: currentColor; }

.arrow--next .icon { transform: rotate(180deg); }

.slider-counter-sm { --value: -80%; position: relative; font-family: 'proximanovat-thin', Arial, sans-serif; color: #939598; }

.slider-counter-sm__current { display: inline-block; vertical-align: middle; }

.slider-counter-sm__all { display: inline-block; vertical-align: middle; }

.slider-counter-sm__line { display: inline-block; vertical-align: middle; width: 60px; background-color: currentColor; height: 1px; position: relative; overflow: hidden; }

.slider-counter-sm__line::before { content: ''; background-color: #b9282e; position: absolute; height: 100%; left: 0; top: 0; width: 100%; transform: translate(var(--value), 0); transition: transform 0.3s; }

.custom-select { --padding: 10px; min-width: 90px; position: relative; font-size: 14px; font-family: 'proximanova-light', Arial, sans-serif; }

.custom-select select { position: absolute; visibility: hidden; opacity: 0; }

.custom-select.is-open .custom-select__panel { visibility: visible; opacity: 1; }

.custom-select__opener { border: 1px solid #d7d7d7; position: relative; padding: calc(var(--padding) / 3) var(--padding); cursor: pointer; user-select: none; }

.custom-select__opener::before { content: ''; width: 8px; height: 8px; border-left: 1px solid #b9282e; border-bottom: 1px solid #b9282e; transform: rotate(45deg); position: absolute; top: 50%; transform: translate(0, -50%) rotate(-45deg); right: .5em; pointer-events: none; }

.custom-select__panel { position: absolute; width: 100%; top: 100%; left: 0; visibility: hidden; opacity: 0; z-index: 1; background-color: #fff; overflow: auto; max-height: 300px; border: 1px solid #d7d7d7; transition: opacity 0.3s, visibility 0.3s; }

.custom-select__option { cursor: pointer; padding: calc(var(--padding) / 3) var(--padding); transition: background-color 0.3s; }

.no-touch .custom-select__option:hover { background-color: #939598; }

.is-col-6 { max-width: 50%; }

.is-col-5 { max-width: 41.66667%; }

.is-col-6-5 { max-width: 54.16667%; }
