.item {
  padding: 15px;
  box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.12);
  background-color: $white;
  @include flex(space-between, normal, column);
  &__img {
    text-align: center;
    img {
      display: inline-block; } }

  &__img,
  &__content {
    border-bottom: 1px solid  $c-border;
    display: block;
    padding-top: 10px;
    padding-bottom: 10px; }
  &__content {
    flex-grow: 1; }
  &__bottom {
    @include flex(space-between, center);
    padding-top: 10px;
    @include xllopen {
      display: block;
      .btn {
        min-width: 0;
        width: 100%; } } } }

.item-list {
  dl {
    @include clr; }
  dt {
    @include light;
    font-size: $fz-md;
    color: $gray;
    float: left;
    margin-right: 5px; }
  dd {
    @include reg;
    font-size: $fz-md; }
  em {
    font-style: normal;
    color: $red; } }

