.controls {
  @include flex(flex-start, center, row, wrap);
  &__title {
    display: inline-block;
    text-transform: uppercase;
    margin-right: 5px; }
  &__text {
    display: inline-block;
    color: $red;
    text-transform: uppercase; } }

.controls__item {
  width: 33.33333%;
  padding-left: 2%;
  padding-right: 1.5%;
  @include xllopen {
    width: 100%;
    padding-left: 10px;
    padding-right: 10px;
    .select {
      justify-content: flex-start;
      label {
        margin-right: 10px; } }
    @include notlast {
      border: 0;
      margin-bottom: 10px; } }
  @include xl {
    width: 100%;
    padding-left: 10px;
    padding-right: 10px;
    .select {
      justify-content: flex-start;
      label {
        margin-right: 10px; } }
    @include notlast {
      border: 0;
      margin-bottom: 10px; } }
  @include reg;
  @include notlast {
    border-right: 1px solid  $c-border; }
  .select {
    @include flex(space-between, center);
    label {
      @include bold;
      font-size: $fz-md; } } }

