h1, h2, h3, h4, h5, h6 {
  font-weight: 400; }

.title {
  font-size: 36px;
  color: $black;
  @include reg;
  line-height: 1.2;
  text-transform: uppercase;
  strong {
    @include bold;
    color: $red; }
  &--white {
    color: $white; } }

.h1, h1 {
  font-size: 36px;
  color: $white;
  @include bold;
  line-height: 1.2;
  text-transform: uppercase; }

.h2, h2 {
  text-transform: uppercase;
  color: $white;
  @include bold;
  font-size: 60px;
  line-height: 1.2;
  @include css-lock(40, 60, 576, 1199, font-size); }


.h2-subttl {
  font-size: 32px;
  @include thin;
  color: $white;
  line-height: 1.2;
  @include css-lock(24, 32, 576, 1199, font-size); }

.h3, h3 {
  @include bold;
  @include css-lock(28, 38, 576, 1199, font-size);
  line-height: 1;
  &--blue {
    color: $blue; }
  &--ttu {
    text-transform: uppercase; }
  &--md {
    @include css-lock(26, 36, 576, 1199, font-size); }
  &--light {
    @include light; }
  &--sm {
    @include bold;
    text-transform: uppercase;
    @include css-lock(18, 21, 576, 1199, font-size); } }

.h3-subttl {
  font-size: 21px;
  color: $black;
  text-transform: uppercase;
  @include light;
  @include css-lock(18, 21, 576, 1199, font-size);
  line-height: 1; }

.h4, h4 {
  text-transform: uppercase;
  @include light;
  color: $white;
  font-size: 18px;
  @include sm {
    font-size: $fz; }

  &--blue {
    color: $blue; }
  &--ttn {
    text-transform: none; }
  &--lg {
    @include bold;
    @include css-lock(21, 26, 576, 1199, font-size); }
  &--md {
    @include reg;
    @include css-lock(21, 24, 576, 1199, font-size); }
  &--black {
    color: $black; } }

.h5, h5 {}

.h6, h6 {}

.text {
  font-size: $fz;
  text-align: justify;
  @include light;
  color: $c-text;
  @include md {
    font-size: $fz-md; }
  &--thin {
    @include thin; }
  &--gray {
    color: $gray;
    strong {
      color: $c-text; } } }

.title-under {
  position: relative;
  padding-bottom: 30px;
  @include after {
    border-bottom: 1px solid  $red;
    width: 50px;
    left: 50%;
    transform: translate(-50%, 0);
    bottom: 0;
    position: absolute; }
  @include before {
    border-bottom: 1px solid  $red;
    width: 100px;
    left: 50%;
    transform: translate(-50%, 0);
    bottom: 8px;
    position: absolute; } }
